.slider {
  position: relative;
  min-height: 700px;
  height: 90vh;
}

.slider-big {
  height: 100%;
  cursor: grab;
  > * {
    height: 100%;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.slider-smoll {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    background: #fff;
    padding: 3.5rem 4rem 2rem;
    max-width: 500px;
    height: 550px;

    pointer-events: none;
    z-index: 999;
		/* .slider-smoll__title */
		&__title {
      text-transform: uppercase;
      font-weight: 300;
      color: #364d59;
      margin-bottom: 1rem;
    }
		/* .slider-smoll__text */
		&__text {
      margin-bottom: 3rem;
    }
		/* .slider-smoll__btn */
		&__btn {
      display: inline-block;
      font-weight: 400;
      color: #212529;
      text-align: center;
      vertical-align: middle;
      background-color: #0000;
      border: 1px solid #0000;
      padding: 0.375rem 0.75rem;
      font-size: 1rem;
      line-height: 1.5;
      border-radius: 0.25rem;
      color: #fff;
      background-color: #43ab92;
      border-color: #43ab92;
    }
}

.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
  background-color: #fff;
  border: 2px solid rgba(0, 0, 0, 0.3);
  &.swiper-pagination-bullet-active {
    background-color: #43ab92;
  }
}
