.pulse {
  animation: pulse 1.5s ease-in-out .5s infinite;
}
.skeleton-header {
  width: 35%;
  height: 16px;
  background-color: #c5c5c5;
}
.skeleton-block {
  height: 35px;
  width: 100%;
  background-color: #c5c5c5;
  margin-top: 15px;
  margin-bottom: 30px;
}

@keyframes pulse {
  0% {
      opacity: 1
  }
  50% {
      opacity: .4
  }
  100% {
      opacity: 1
  }
}