.popup {
  position: fixed;
  background-color: rgba(212, 209, 209, 0.8);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 9999;

  display: flex;
  align-items: center;
  justify-content: center;
  // transform: translate(-50%, -50%);
}

.popup-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 600px;
  height: 300px;
  padding: 20px 40px 20px;
  background-color: #fff;
}

.popup-content {
  min-width: 100%;
  margin: 70px 0 0;
  > p {
    text-align: center;
    font-size: 30px;
    width: 100%;
  }
}

.popup-close {
  width: 25px;
  height: 25px;
  cursor: pointer;
  img {
    width: 100%;
    height: 100%;
  }
}